<template>
  <scroll :data="topList" class="toplist" :pullup="true" :beforeScroll="true" @scrollToEnd="serachMove" @beforeScroll="listScroll" ref="toplist">
    <ul ref="rank">
      <li @click="selectItem(item)" class="item" v-for="(item, index) in topList" :key="index">
        <div class="icon">
          <img v-lazy="item.cover"/>
        </div>
        <!-- <ul class="songlist"> -->
          <!-- <li class="song"> -->
            <div class="discList-con">
              <h2>{{item.name}}</h2>
              <p>{{item.artistName}}</p>
            </div>
            <!-- <span>{{index + 1}}</span> -->
            <!-- <span>{{item.name}}-{{item.artistName}}</span> -->
          <!-- </li> -->
        <!-- </ul> -->
      </li>
      <loading v-show="hasMove" title=""></loading>
      <p class="lodAuto" v-show="!hasMove">(^_^) 已全部加载</p>
    </ul>
    <!-- <div class="loading-container" v-show="!topList.length">
      <loading></loading>
    </div> -->
    <transition name="fade">
      <router-view></router-view>
    </transition>
  </scroll>
</template>

<script>
import { mapMutations } from "vuex";
import Scroll from "base/scroll/scroll";
import Loading from "base/loading/loading";
import { getTopList } from "api/rank";
// import { ERR_OK } from "api/config";
import { playlistMixin } from "assets/js/mixin";
const ERR_OK = 200;
const perpage = 30; // 每次访问条数
export default {
  name: "Rank",
  mixins: [playlistMixin],
  components: {
    Scroll,
    Loading
  },
  data() {
    return {
      page: 1,
      hasMove: true,
      ListCount: 1000,
      topList: [],
      hotList: [
        { index: "0",
          value: "云音乐新歌榜"
        },
        { index: "1",
          value: "云音乐热歌榜"
        },
        { index: "2",
          value: "网易原创歌曲榜"
        },
        { index: "3",
          value: "云音乐飙升榜"
        },
        { index: "4",
          value: "云音乐电音榜"
        },
        { index: "5",
          value: "UK排行榜周榜"
        },
        { index: "6",
          value: "美国Billboard周榜"
        },
        { index: "7",
          value: "KTV嗨榜"
        },
        { index: "8",
          value: "iTunes榜"
        },
        { index: "9",
          value: "Hit FM Top榜"
        },
        { index: "10",
          value: "日本Oricon周榜"
        },
        { index: "11",
          value: "韩国Melon排行榜周榜"
        },
        { index: "12",
          value: "韩国Mnet排行榜周榜"
        },
        { index: "13",
          value: "韩国Melon原声周榜"
        },
        { index: "14",
          value: "中国TOP排行榜(港台榜)"
        }
      ]
    };
  },
  created() {
    this._getTopList();
  },
  methods: {
    handlePlaylist(playlist) {
      if (this.$refs.rank) {
        const bottom = playlist.length > 0 ? "60px" : "";

        this.$refs.rank.style.paddingBottom = bottom;
        this.$refs.toplist.refresh();
      }
    },
    selectItem(item) {
      this.$router.push({
        path: `/rank/${item.id}`
      });
      this.setTopList(item);
    },
    _getTopList() {
      getTopList(perpage, this.page).then(res => {
        console.log(res);
        if (res.code === ERR_OK) {
          this.topList = res.data;
          this._checkMore(this.topList);
        }
      });
    },
    /* 判断当前是否已经加载全部内容 */
    _checkMore(data) {
      console.log(data);
      if (data.length >= this.ListCount) {
        this.hasMove = false;
      }
    },
    serachMove() {
      if (!this.hasMove) {
        return;
      }
      this.page++; // 下拉加载，请求页数增加
      getTopList(perpage, this.page * perpage).then(res => {
        console.log(res);
        if (res.code === ERR_OK) {
          if (res.data.length === 0) {
            this.hasMove = false;
            return;
          }
          this.topList = this.topList.concat(res.data);
          this._checkMore(this.topList);
          // this.result = this._genResult(res.result.songs);
          // this._checkMore(res.result.songs);
        }
        // if (res.code === ERR_OK) {
        //   this.result = this.result.concat(this._genResult(res.result.songs));
        //   this._checkMore(res.result.songs);
        // }
      });
    },
    listScroll() {
      this.$emit("listScroll");
    },
    ...mapMutations({
      setTopList: "SET_TOP_LIST"
    })
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "assets/css/mixin.scss";
// .rank {
//   position: fixed;
//   width: 100%;
//   top: rem(88);
//   bottom: 0;
// }
.toplist {
  // height: 100%;
  width: 100vw;
  height: 86vh;
  overflow: hidden;
}
.item {
  display: flex;
  margin: 0 rem(20);
  padding: rem(10) 0;
  height: rem(100);
}
.item:last-child {
  padding-bottom: rem(20);
}
.icon {
  flex: 0 0 rem(100);
  width: rem(100);
  height: rem(100);
}
.icon img {
  max-width: 100%;
  // max-height: 100%;
  height: auto;
  border-radius: rem(10);
}
.songlist {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 rem(20);
  height: rem(100);
  overflow: hidden;
  background: #333;
  color: rgba(255, 255, 255, 0.486);
  font-size: rem(13);
}
.song {
  max-height: rem(26);
  line-height: rem(26);
  overflow: hidden;
}
.song span {
  width: 100%;
  overflow: hidden;
}
.discList-con {
  padding-left: rem(20);
}
.discList-con h2 {
  margin-bottom: rem(10);
  color: #fff;
  font-size: rem(15);
  // padding-top: rem(16);
  line-height: rem(25);

}
.discList-con p {
  color: hsla(0, 0%, 100%, 0.3);
  font-size: rem(14);
}
.loading-container {
  position: fixed;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}
.lodAuto {
  text-align: center;
  margin-top: rem(10);
  color: rgba(206, 209, 212, 0.849);
  border-top: 1px solid rgba(255, 255, 255, 0.082);
  line-height: rem(40);
  font-size: rem(20)
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s;
}
.fade-enter,
.fade-leave-to {
  transform: translate3d(100%, 0, 0);
}
</style>



